body {
  background-color: #C3C3C3;
}

.login-box {
  width: 450px;
  display: block;
  margin: 100px auto;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 30px 40px;
  border-radius: 5px;
}

.login-logo img {
  display: block;
  width: 240px;
  margin: 15px auto 40px;
}

.copyright {
  display: block;
  color: #777;
  text-align: center;
  font-size: 14px;
}

.copyright a {
  color: #777;
}